import { ElementBuilder } from "./ElementBuilder"
import { WallElement, WallElementProperties, WallElementSize } from "./WallElement"

export type NoteProperties = WallElementProperties & {
    content: string,
    background: string
}

export class Note extends WallElement<NoteProperties> {
	getSize(): WallElementSize {
		return {
			width: 160,
			height: 160
		}
	}
}

export class NoteBuilder extends ElementBuilder<Note, NoteProperties> {
	properties = {
		position: {
			x: 0,
			y: 0
		},
		content: '',
		background: ''
	}

	buildElement () {
		return new Note(this.id, this.properties)
	}
}
