import { Application, Canvas as ICanvas, WallElementId } from "@eswall/core"
import Konva from 'konva'
import { SelectionRectangle } from "./SelectionReactangle"
import { SelectedElementsWrapper } from "./SelectedElementsWrapper"
import { CanvasElementsManager } from "./CanvasElementsManager"
import { getWrapperRect } from "./utils/getWrapperRect"
import { ShortcutManager } from "./ShortcutManager"
import { KonvaEventObject } from "konva/lib/Node"
import { Vector2d } from "konva/lib/types"

export class Canvas extends Konva.Stage implements ICanvas {
	layer : Konva.Layer
	elementsManager !: CanvasElementsManager
	selectedElementsWrapper !: SelectedElementsWrapper
	selectionArea !: SelectionRectangle

	private movePositionStart : {
		cursor: Vector2d,
		canvas: Vector2d
	} | null = null

	private mouseMoveHandleFn: ((event: MouseEvent) => void) | null = null

	constructor (
		container: HTMLDivElement,
		public shortcutManager: ShortcutManager
	) {
		super({
			container,
			width: container.offsetWidth,
			height: container.offsetHeight
		})

		this.layer = new Konva.Layer()
		this.add(this.layer)

		this.container().addEventListener('wheel', this.wheelHandler.bind(this))
		window.addEventListener('resize', this.windowResizeHandler.bind(this))

		this.on('mousedown', this.mouseDownHandler.bind(this))
		document.body.addEventListener('mouseup', this.mouseUpHandler.bind(this))
	}

	setApplication (app: Application) {
		this.selectedElementsWrapper = new SelectedElementsWrapper(this, app)
		this.elementsManager = new CanvasElementsManager(app, this)
		this.selectionArea = new SelectionRectangle(this, app)
	}

	getCanvasCenterPosition(): { x: number; y: number } {
		return {
			x: (-this.x() + (this.width() / 2)) / this.scaleX(),
			y: (-this.y() + (this.height() / 2)) / this.scaleX()
		}
	}

	getCursorPosition(): { x: number; y: number } {
		return this.getPointerAbsolutePosition()
	}

	public turnOnAddElementOnClickMode(): Promise<{ x: number; y: number }> {
		return new Promise(resolve => {
			const handler = (e: MouseEvent) => {
				e.stopPropagation()
				const { x, y } = this.getPointerAbsolutePosition()

				this.container().removeEventListener('click', handler)

				const elementPosition = {
					x: x - 80,
					y: y - 80
				}

				resolve(elementPosition)
			}
			this.container().addEventListener('click', handler)
		})
	}

	public activeElementWithId (id: WallElementId) {
		const canvasElement = this.elementsManager.findElementById(id)

		if (!canvasElement) return

		canvasElement.click()
	}

	public addToLayer (element: Konva.Shape | Konva.Group) {
		return this.layer.add(element)
	}

	public getPointerAbsolutePosition() : { x: number, y: number } {
		const position = { x: 0, y: 0 }
		const pointerPosition = this.getPointerPosition()

		if (!pointerPosition) return position

		position.x = (pointerPosition.x - this.x()) / this.scaleX()
		position.y = (pointerPosition.y - this.y()) / this.scaleX()

		return position
	}

	private windowResizeHandler () {
		this.width(this.container().offsetWidth)
		this.height(this.container().offsetHeight)
	}

	private wheelHandler (event: WheelEvent) {
		event.preventDefault()

		if (event.ctrlKey) this.zoomUsingWheel(event)
		else this.moveUsingWheel(event)
	}

	private moveUsingWheel(event: WheelEvent) {
		const shiftKey = event.shiftKey

		const newPos = {
			x: this.x() - (shiftKey ? event.deltaY : event.deltaX),
			y: this.y() - (shiftKey ? event.deltaX : event.deltaY)
		}
		this.position(newPos)
		this.fire('move')
	}

	private zoomUsingWheel (event: WheelEvent) {
		const scaleBy = 1.12
		const oldScale = this.scaleX()
		const pointer = this.getPointerPosition()

		if (!pointer) return

		const mousePointTo = {
			x: (pointer.x - this.x()) / oldScale,
			y: (pointer.y - this.y()) / oldScale
		}

		const direction = event.deltaY > 0 ? -1 : 1

		let newScale = direction > 0 ? oldScale * scaleBy : oldScale / scaleBy

		if (newScale < 0.1) newScale = 0.1
		if (newScale > 2) newScale = 2

		this.scale({ x: newScale, y: newScale })

		const newPos = {
			x: pointer.x - mousePointTo.x * newScale,
			y: pointer.y - mousePointTo.y * newScale
		}
		this.position(newPos)
		this.fire('zoom')
	}

	private isMoveEvent (event: KonvaEventObject<MouseEvent>) {
		if (event.target !== this) return false

		const wheelClick = event.evt.button === 1
		if (event.evt.ctrlKey || wheelClick) return true

		return false
	}

	private getMouseCoordinates (event: MouseEvent) {
		return {
			x: event.clientX,
			y: event.clientY
		}
	}

	private mouseDownHandler (event: KonvaEventObject<MouseEvent>) {
		if (!this.isMoveEvent(event)) return this.selectionArea.mouseDownHandler(event)
		event.evt.preventDefault()

		this.mouseMoveHandleFn = this.mouseMoveHandler.bind(this)
		document.body.addEventListener('mousemove', this.mouseMoveHandleFn)
		this.movePositionStart = {
			cursor: this.getMouseCoordinates(event.evt),
			canvas: {
				x: this.x(),
				y: this.y()
			}
		}
	}

	private mouseMoveHandler (event: MouseEvent) {
		if (!this.movePositionStart) return
		event.preventDefault()

		const position = this.getMouseCoordinates(event)

		this.x(this.movePositionStart.canvas.x + position.x - this.movePositionStart.cursor.x)
		this.y(this.movePositionStart.canvas.y + position.y - this.movePositionStart.cursor.y)
	}

	private mouseUpHandler () {
		this.movePositionStart = null
		if (this.mouseMoveHandleFn) document.removeEventListener('mousemove', this.mouseMoveHandleFn)
	}

	public toPNGDataURL () : string {
		const oldScale = this.scaleX()
		this.scale({ x: 1, y: 1 })

		const rect = getWrapperRect(this, this.elementsManager.elements, 20)

		const dataURL = this.toDataURL({
			pixelRatio: 0.5,
			x: rect.x + this.position().x,
			y: rect.y + this.position().y,
			width: rect.width,
			height: rect.height
		})

		this.scale({ x: oldScale, y: oldScale })

		return dataURL
	}

	public async toUint8Array(): Promise<Uint8Array> {
		const oldScale = this.scaleX()
		this.scale({ x: 1, y: 1 })

		const rect = getWrapperRect(this, this.elementsManager.elements, 20)

		const blob = (await this.toBlob({
			pixelRatio: 0.5,
			x: rect.x + this.position().x,
			y: rect.y + this.position().y,
			width: rect.width,
			height: rect.height
		}) as Blob)

		this.scale({ x: oldScale, y: oldScale })

		const buffer = await blob.arrayBuffer()

		return new Uint8Array(buffer)
	}
}
