import { AddElementsCommand, ChangeElementsPropertiesCommand, RemoveElementsCommand } from "../commands"
import { CommandHistory, Wall } from "../core"
import { Note, NoteProperties, WallElement } from "../elements"
import { WallElementId } from "../value_objects"

export class SelectService {
	constructor (
		private wall : Wall,
		private commandHistory: CommandHistory
	) {}

	addSelectedElement (element: WallElement) {
		this.wall.addSelectedElement(element)
	}

	selectAll () {
		this.wall.selectAll()
	}

	selectElementsByIds(ids: WallElementId[]) {
		this.wall.selectByIds(ids)
	}

	getSelectedElementsIds () {
		return this.wall.getSelectedElementsIds()
	}

	removeSelected() {
		const ids = this.getSelectedElementsIds()
		if (ids.length === 0) return
		const command = new RemoveElementsCommand(ids)
		this.commandHistory.executeCommand(command)
	}

	duplicateSelectedElements(): void {
		const elements = this.wall.selectedElements

		const duplicatedElements = elements
			.map((element: WallElement) => element.duplicate())

		duplicatedElements.forEach((element: WallElement) => {
			element.move(20, 20)
		})


		const command = new AddElementsCommand(duplicatedElements)
		this.commandHistory.executeCommand(command)

		this.wall.selectElements(duplicatedElements)
	}

	changeColorForSelectedNotes(color: string) {
		const elements = this.wall.getSelectedElements()
		const notes = elements.filter(element => element instanceof Note) as Note[]

		const idAndPropertiesArray = notes.map(note => {
			const properties = note.getProperties()
			properties.background = color
			return [note.id, properties]
		}) as Array<[WallElementId, NoteProperties]>

		const command = new ChangeElementsPropertiesCommand(idAndPropertiesArray)

		this.commandHistory.executeCommand(command)
	}
}
