import { Command } from "../commands"
import { EventBus, Event } from "../shared/EventBus"
import { Wall } from "./Wall"

export class HistoryChangedEvent extends Event {}

export type CommandHistoryEventsType =
	| HistoryChangedEvent

export const CommandHistoryEvents : CommandHistoryEventsType[] = [HistoryChangedEvent]
export class CommandHistory {
	previewsCommands : Command[]  = []
	nextCommands : Command[] = []

	constructor (
		private eventBus: EventBus,
		private wall: Wall
	) {}

	undo() {
		const command = this.previewsCommands.pop()
		if (!command) return

		const result = command.undo(this.wall)

		if (result.isError()) return
		this.nextCommands.push(command)

		this.eventBus.notify(HistoryChangedEvent)
	}

	redo () {
		const command = this.nextCommands.pop()
		if (!command) return

		const result = command.execute(this.wall)

		if (result.isError()) return
		this.previewsCommands.push(command)

		this.eventBus.notify(HistoryChangedEvent)
	}

	executeCommand(command: Command) {
		const result = command.execute(this.wall)
		if (result.isError()) return
		this.previewsCommands.push(command)
		this.nextCommands = []

		this.eventBus.notify(HistoryChangedEvent)
	}

	isUndoAvailable () {
		return this.previewsCommands.length > 0
	}

	isRedoAvailable () {
		return this.nextCommands.length > 0
	}
}
