import { AddElementsCommand, Command } from "../commands"
import { NoteBuilder, WallElement, WallElementProperties } from "../elements"
import { Publisher, EventBus } from "../shared/EventBus"
import { WallElementId } from "../value_objects"
import { Canvas } from "../adapters/Canvas"
import { CommandHistory } from "./CommandHistory"
import { Wall } from "./Wall"
import { seedElementOnSquareOnCenterOfPosition } from "../utils/WallElementsUtils"
import { SerializedElement } from "./Serializer"
import { ClipboardService } from "../services/ClipboardService"
import { SelectService } from "../services/SelectService"
import { Clipboard } from "../adapters/Clipboard"
import { ElementBuilder } from "../elements/ElementBuilder"

export class Application implements Publisher {
	private eventBus: EventBus = new EventBus()
	subscribe = this.eventBus.subscribe.bind(this.eventBus)
	unsubscribe = this.eventBus.unsubscribe.bind(this.eventBus)

	private wall: Wall = new Wall([], this.eventBus)
	private commandHistory: CommandHistory = new CommandHistory(this.eventBus, this.wall)
	private clipboardService: ClipboardService
	private selectService: SelectService


	constructor(
		private canvas: Canvas,
		private clipboard: Clipboard,
	) {
		this.selectService = new SelectService(this.wall, this.commandHistory)

		this.clipboardService = new ClipboardService(
			this.eventBus,
			this.wall,
			this.canvas,
			this.commandHistory,
			this.selectService,
			this.clipboard
		)
	}

	executeCommand(command: Command) {
		this.commandHistory.executeCommand(command)
	}

	undo() {
		this.commandHistory.undo()
	}

	redo() {
		this.commandHistory.redo()
	}

	isUndoAvailable () {
		return this.commandHistory.isUndoAvailable()
	}

	isRedoAvailable () {
		return this.commandHistory.isRedoAvailable()
	}

	exportPNGDataURL () {
		return this.canvas.toPNGDataURL()
	}

	exportSerializedData () : SerializedElement[] {
		return this.wall.exportElements()
	}

	exportUint8Array () : Promise<Uint8Array> {
		return this.canvas.toUint8Array()
	}

	import(serializedElements: SerializedElement[]) {
		const elements = this.wall.deserializeElements(serializedElements)

		const command = new AddElementsCommand(elements)
		this.executeCommand(command)

		this.wall.selectElements(elements)
	}

	async createWallElementOnCanvasClickedPoint (builder: ElementBuilder<WallElement, WallElementProperties>) {
		const position = await this.canvas.turnOnAddElementOnClickMode()

		const element = builder.mergeProperties({
			position
		}).build()

		const command = new AddElementsCommand([ element ])
		this.executeCommand(command)

		this.canvas.activeElementWithId(element.id)
	}

	createManyNotesOnCenter (contents: string[], color: string) {
		const notes = contents.map(content => new NoteBuilder()
			.mergeProperties({
				content,
				background: color
			})
			.build()
		)

		const center = this.canvas.getCanvasCenterPosition()
		seedElementOnSquareOnCenterOfPosition(notes, center, 20)

		const command = new AddElementsCommand(notes)
		this.executeCommand(command)

		this.wall.selectElements(notes)
	}

	// Select Service facade
	addSelectedElement (element: WallElement) {
		this.selectService.addSelectedElement(element)
	}

	selectAll () {
		this.selectService.selectAll()
	}

	selectElementsByIds(ids: WallElementId[]) {
		this.selectService.selectElementsByIds(ids)
	}

	getSelectedElementsIds () {
		return this.selectService.getSelectedElementsIds()
	}

	removeSelected() {
		this.selectService.removeSelected()
	}

	duplicateSelectedElements() {
		this.selectService.duplicateSelectedElements()
	}

	changeColorForSelectedNotes(color: string) {
		this.selectService.changeColorForSelectedNotes(color)
	}

	// Clipboard Service facade
	copySelectedElements () {
		this.clipboardService.copySelectedElements()
	}

	paste () {
		this.clipboardService.paste()
	}

	pasteOnCursorPosition () {
		this.clipboardService.pasteOnCursorPosition()
	}

	isPasteAvailable () {
		return this.clipboardService.isPasteAvailable()
	}

	cutSelectedElements () {
		this.clipboardService.cutSelectedElements()
	}
}
