import { ZIndexManager } from "../core/ZIndexManager"
import { WallElementId } from "../value_objects"
import { WallElement, WallElementProperties } from "./WallElement"

// https://stackoverflow.com/questions/27936772/how-to-deep-merge-instead-of-shallow-merge
function mergeDeep(...objects: any[]) {
	const isObject = (obj: any) => obj && typeof obj === 'object'

	return objects.reduce((prev, obj) => {
		Object.keys(obj).forEach(key => {
			const pVal = prev[key]
			const oVal = obj[key]

			if (Array.isArray(pVal) && Array.isArray(oVal)) {
				prev[key] = pVal.concat(...oVal)
			}
			else if (isObject(pVal) && isObject(oVal)) {
				prev[key] = mergeDeep(pVal, oVal)
			}
			else {
				prev[key] = oVal
			}
		})

		return prev
	}, {})
}

export abstract class ElementBuilder <T extends WallElement, P extends WallElementProperties> {
	protected id : WallElementId = WallElementId.generate()
	protected abstract properties : P
	abstract buildElement () : T

	build () : WallElement {
		return this.buildElement()
	}

	setId (id: WallElementId) {
		this.id = id
		return this
	}

	mergeProperties (properties: Partial<P>) {
		this.properties = mergeDeep(this.properties, properties)
		return this
	}
}
