import { AddElementsCommand } from "../commands"
import { Canvas, Clipboard, CommandHistory, SerializedElement, Serializer, Wall } from "../core"
import { EventBus } from "../shared/EventBus"
import { centerElementsOnCenterOfPosition } from "../utils/WallElementsUtils"
import { SelectService } from "./SelectService"
import { Event } from "../shared/EventBus"


export class ClipboardElementsChanged extends Event { }
export class CutEvent extends Event { }
export class CopyEvent extends Event { }

export type ClipboardEventsType = ClipboardElementsChanged |
	CutEvent |
	CopyEvent

export const ClipboardEvents: ClipboardEventsType[] = [ClipboardElementsChanged, CutEvent, CopyEvent]


export class ClipboardService {

	constructor (
		private eventBus: EventBus,
		private wall : Wall,
		private canvas: Canvas,
		private commandHistory: CommandHistory,
		private selectService: SelectService,
		private clipboard: Clipboard,
		private serializer: Serializer = new Serializer()
	) {}

	copySelectedElements () {
		const selectedElements = this.wall.selectedElements
		const serializedElements = this.serializer.serialize(selectedElements)
		this.clipboard.write(serializedElements)
		this.eventBus.notify(new ClipboardElementsChanged())
		this.eventBus.notify(new CopyEvent())
	}

	paste () {
		const serializedElements = this.clipboard.read()
		const elements = this.serializer
			.deserialize(serializedElements)
			.map(element => element.duplicate())

		const command = new AddElementsCommand(elements)
		this.commandHistory.executeCommand(command)

		this.wall.selectElements(elements)
	}

	pasteOnCursorPosition () {
		const position = this.canvas.getCursorPosition()
		const serializedElements = this.clipboard.read()

		const elements = this.serializer
			.deserialize(serializedElements)
			.map(element => element.duplicate())

		centerElementsOnCenterOfPosition(elements, position)

		const command = new AddElementsCommand(elements)
		this.commandHistory.executeCommand(command)

		this.wall.selectElements(elements)
	}

	isPasteAvailable () {
		const serializedElements = this.clipboard.read()
		return serializedElements.length > 0
	}

	cutSelectedElements () {
		const selectedElements = this.wall.selectedElements
		const serializedElements = this.serializer.serialize(selectedElements)
		this.clipboard.write(serializedElements)
		this.selectService.removeSelected()
		this.eventBus.notify(new ClipboardElementsChanged())
		this.eventBus.notify(new CutEvent())
	}
}
