import { Canvas } from '@eswall/canvas/src/Canvas'
import { Application } from '@eswall/core'
import { ClipboardInMemory } from '@eswall/core/src/adapters/ClipboardInMemory'
import { ShortcutManager } from '@eswall/shortcuts/src/ShortcutManager'

export function buildApp (container: HTMLDivElement): Application {
	const shortcutManager = new ShortcutManager()
	const canvas = new Canvas(container, shortcutManager)
	const app = new Application(
		canvas,
		new ClipboardInMemory()
	)
	shortcutManager.setApplication(app)
	canvas.setApplication(app)

	return app
}
