export type PublisherEventCallback <T extends Event> = (event: T) => void

export interface Publisher {
	subscribe <T extends Event> (Event : Event, callback : PublisherEventCallback<T>) : void
	unsubscribe <T extends Event> (Event : Event, callback : PublisherEventCallback<T>) : void
}

export class EventBus implements Publisher {
	// eslint-disable-next-line @typescript-eslint/no-explicit-any
	private readonly callbacks : { [key: string] : Array<PublisherEventCallback<any>> } = {}

	subscribe <T extends Event> (Event : Event, callback : PublisherEventCallback<T>) {
		if (!this.callbacks[Event.type]) this.callbacks[Event.type] = []

		this.callbacks[Event.type].push(callback)
	}

	unsubscribe <T extends Event> (Event : Event, callback : PublisherEventCallback<T>) {
		if (!this.callbacks[Event.type]) return

		this.callbacks[Event.type] = this.callbacks[Event.type].filter(cb => cb !== callback)
	}

	notify (event : Event) {
		const callbacksArr = this.callbacks[event.type]

		if (!callbacksArr) return

		callbacksArr.forEach(callback => callback(event))
	}
}

export abstract class Event {
	static get type () {
		return this.name
	}
	get type () {
		return (this.constructor as typeof Event).type
	}
}
