import { Application } from '@eswall/core'
import {html} from 'lit'
import {customElement, query} from 'lit/decorators.js'
import { FileAdapter } from '@eswall/adapters-file'
import { TextInput } from '../Input'
import { Modal } from './Modal'

// function from https://stackoverflow.com/a/15832662/512042
function downloadURI(uri: string, name: string) {
	const link = document.createElement('a')
	link.download = name
	link.href = uri
	document.body.appendChild(link)
	link.click()
	document.body.removeChild(link)
}

@customElement('ui-save-file-modal')
export class SaveFileModal extends Modal {
	static open (app: Application) {
		const modal = super.createAndAppend<SaveFileModal>('ui-save-file-modal')
		modal.init(app)
	}

	@query('ui-text-input') input !: TextInput
	title = 'Save file'

	private app!: Application
	private fileAdapter!: FileAdapter

	init (app: Application) {
		this.app = app
		this.fileAdapter = new FileAdapter(this.app)
	}

	private async save () {
		const filename = this.input.getValue()
		if (!filename) return ''

		const filenameWithExt = filename + '.eswall.png'
		const png = await this.fileAdapter.exportPNGDataURL()

		downloadURI(png, filenameWithExt)

		this.close()
	}

	body () {
		return html`<div>
			<ui-text-input label="File name"></ui-text-input>
			<ui-button @click="${this.save}">Save</ui-button>
		</div>`
	}
}
